.languageSwitcher{display:flex;align-items:center;gap:.5rem;font-size:.75rem;background:hsla(0,0%,100%,.1);border:2px solid hsla(0,0%,100%,.2);backdrop-filter:blur(10px);padding:.5rem;transition:all .6s cubic-bezier(0.25, 0.46, 0.45, 0.94);box-shadow:0 2px 8px rgba(0,0,0,.08)}.languageSwitcher:hover{background:rgba(102,204,153,.15);border-color:rgba(102,204,153,.3);transform:scale(1.05);box-shadow:0 4px 12px rgba(102,204,153,.2)}.langButton{background:none;border:none;color:#000;cursor:pointer;padding:.25rem .5rem;transition:all .6s cubic-bezier(0.25, 0.46, 0.45, 0.94);font-size:.75rem;font-weight:300;text-transform:uppercase;letter-spacing:.05em}.langButton:hover{transform:scale(1.1)}.langButton.active{color:#6c9;font-weight:600;transform:scale(1.05)}.separator{color:rgba(0,0,0,.3);font-weight:300;font-size:.8rem}